@import '../../style/communs.scss';
$dark: #272727;
$light: #fbfbfb;

$animation-speed-fast: .8s;
$animation-speed-slow: 1s;



.titlePresta{
    color: rgba(247, 191, 124);
    font-size: 30px;
    font-weight: bold;
    margin-left: 128px;
    margin-top: 20px;
   
  }
.containerPrestations{
    display: flex;
    justify-content:space-between;
    flex-wrap: wrap;
   align-items:center;
    //margin-right: 110px;
    height:auto;
}
.card {
  width: 230px;
  height: 325px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  background: linear-gradient(rgba(247, 191, 124), rgba(254, 213, 49));
  position: relative;
  perspective: 1000px;
  transform: perspective(1000px) translateY(0);
  transform-style: preserve-3d;
  transition: transform $animation-speed-fast ease-in-out;
  
  
    .purchase-button{
      border: 1px solid rgba(247, 191, 124);
    
  }
}

.card-container{
  display: flex;
  margin: 20px;
}

.card-wrapper:hover{
  .card{
    transform: rotateY(179deg);
    z-index: 2;
    
    .text-overlay{
      opacity: 0;
      transform-style: preserve-3d;
      z-index: 1;
    }
    
    .purchase-button-container{
      opacity: 1;
    }
  }
}
.titleh2{
    color: white;
    font-weight: bold;
    font-size:1.4vw;
   width:80%;
}
.text-overlay{
  transform: perspective(1000px) translateY(0) translateZ(80px);
  transition: transform $animation-speed-fast ease-in-out, opacity $animation-speed-slow ease;
  pointer-events: none;
  width: 100%;
  perspective: inherit;
  padding-top: 80px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

h1{
  color: $dark;

}

h2{
  font-size: .9rem;
  text-align: center;
  font-weight: 500;
}
.price, .titleh2Price{
    text-align: center;
    font-size: 1.6rem;
    font-weight: 900;
    
}
.price{
   font-size: 1.8rem;
   margin: 10px;
}
.titleh2Price{
      font-size: 1.6rem;
       margin-top: -20px;
}
.purchase-button-container{
  color: white;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  left: 0;
  top: 0;
  transform: perspective(1000px) rotateY(179deg) translateY(0) translateZ(80px);
  opacity: 0;
  z-index: -1;
  transition: transform $animation-speed-fast ease-in-out, opacity $animation-speed-slow ease;
  width: 100%;
  height: 100%;
  
  .purchase-button{
    padding: 10px 40px;
    border-radius: 50px;
    font-size: 1.2rem;
    transition: background-color .25s ease, color .25s ease;
    cursor: pointer;
      }
}

.details-text{
  margin-top: 20px;
  display: flex;
  gap: 10px 10px;
  align-items: center;
  text-align: center;
  flex-direction: column;
  color: white;
  font-size: 1rem;
}

small{
  font-size: 1rem;
  font-weight: 200;
  color: white;
}



.back-h2{
  font-size: 2rem;
  margin-top: 0;
}

@media #{$medium-down} {

  .titleh2{
    font-size: 20px;
    }
    .titlePresta{
      margin-left: 0px;
    }

 }