
@import '../../style/communs.scss';
footer{
background: linear-gradient(rgba(0, 0, 0, 0) 0%,rgba(74, 71, 95) 100%);
display: flex;
flex-direction: column;
align-items: center;
position: relative;
top: 50px;

}
.containerLogoFooter img{
width:100px;
height:100px;
object-fit: cover;
border-radius: 10px;
}
.containerNavFooter{
    position: relative;
    margin-top:-50px;
    font-size:12px;
}
div.containerNavFooter
{
    padding: 10em ;
	text-align: center;
}

div.containerNavFooter a
{
    color: #FFF;
    text-decoration: none;
    margin: 0px 10px;
    padding: 10px 10px;
    position: relative;
    z-index: 0;
    cursor: pointer;
}


/* Border from Y to X  */
div.borderYtoX a:before, div.borderYtoX a:after
{
    
    position: absolute;
    opacity: 0.5;
    height: 100%;
    width: 2px;
    content: '';
    background: #FFF;
    transition: all 0.3s;
}

div.borderYtoX a:before
{
    left: 0px;
    top: 0px;
}

div.borderYtoX a:after
{
    right: 0px;
    bottom: 0px;
}

div.borderYtoX a:hover:before, div.borderYtoX a:hover:after
{
    opacity: 1;
    height: 2px;
    width: 100%;
}

@media #{$small-down} {
    .container {
        display: flex;
        flex-direction: column;
        font-size: 12px;
    }
    .container a{
        font-size: 8px;
    }

   }